import styled from "styled-components";

const MainMenuAside = styled.aside`
  transition: all;
  -webkit-transition-duration: 250ms;
  transition-duration: 250ms;
  width: 270px;
  position: absolute;
  height: 100%;
  top: 0;
  left: 0;
`;

export default MainMenuAside;



