import styled from "styled-components";

export const Card = styled.div`
  position: relative;
  background: #fff;
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.15);
  margin-bottom: 30px;
  border-radius: 2px;
  min-height: 200px;
  padding: 20px;
`;

export default Card;



